@font-face {
  font-family: "Industry";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Industry-BoldItalic.woff");
}
@font-face {
  font-family: "AcuminProNormal";
  src: url("../fonts/Acumin-RPro.otf");
}
@font-face {
  font-family: "AcuminProBold";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/Acumin-BdItPro.otf");
}
@font-face {
  font-family: "AcuminProItalic";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/Acumin-ItPro.otf");
}
.container-wrapheaderty {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: url("../images/ty-head-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.cont-head-wrpcont {
  min-height: 380px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-family: AcuminProNormal;
}
.cont-head-wrpcont p {
  max-width: 100%;
  width: 700px;
  color: #fff;
  padding: 0 20px 0 20px;
  text-align: center;
  font-size: 17px;
}
.cont-head-wrpcont p:nth-child(3) {
  font-weight: bold;
  margin-top: -10px;
  font-size: 22px;
  text-align: center;
}

.cont-h-wrplogo {
  max-width: 100%;
  height: auto;
  max-height: 140px;
  margin-bottom: 5px;
  padding: 20px;
}

.cont-head-wrpcont-slide {
  position: relative;
  margin-top: 100px;
  background-color: #1c1c28;
}
.cont-head-wrpcont-slide:before {
  content: "";
  display: block;
  position: absolute;
  width: 400px;
  height: 150px;
  top: -141px;
  left: 50%;
  -webkit-transform: translatex(-50%);
          transform: translatex(-50%);
  background: url("../images/ty-head-eco.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: top;
}

.owl-dots {
  display: none;
}